@import "variables.scss";

.price {
  max-width: 1920px;
  margin-top: 80px;

  &-container {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    padding: 0 15px;

    @media (max-width: 1170px) {
      margin-left: 25px;
      margin-right: 25px;
      overflow: hidden;
    }
  }

  &__table-container {
    margin-top: 35px;
  }

  &__table-head {
    background-color: $color-pink;
    margin: 0 -15px;
  }

  &__conditions-head {
    background-color: $color-pink;
    margin: 0 -15px;
  }

  &__cell {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;

    p {
      font-size: 20px;
    }

    &--title {
      padding: 25px;
    }

    &--content {
      padding: 25px 25px 20px 30px;
      border: 1px solid #eaeaea;
    }
  }

  &__conditions-main {
    margin: 0 -15px;
    text-align: center;
  }
}